export const ADD_DATA_SEGMENT_30S = Symbol('ADD_DATA_SEGMENT_30S');
export const ADD_DERIVED_SEGMENT_30S = Symbol('ADD_DERIVED_SEGMENT_30S');
// prettier-ignore
export const ADD_COORDINATES_SEGMENT_30S = Symbol('ADD_COORDINATES_SEGMENT_30S');
export const REMOVE_DATA_SEGMENT_30S = Symbol('REMOVE_DATA_SEGMENT_30S');

export function addDataSegment30s(index, data) {
  return {
    type: ADD_DATA_SEGMENT_30S,
    payload: { index, data },
  };
}

export function addDerivedSegment30s(index, data) {
  return {
    type: ADD_DERIVED_SEGMENT_30S,
    payload: { index, data },
  };
}

export function addCoordinatesSegment30s(index, data) {
  return {
    type: ADD_COORDINATES_SEGMENT_30S,
    payload: { index, data },
  };
}

export function removeDataSegment30s(index) {
  return {
    type: REMOVE_DATA_SEGMENT_30S,
    payload: { index },
  };
}

// Define what should escape rollup tree-shaking, since we package preserved
// file structure to npm.
//
// NODE_ENV condition is for consumer app: it should be able to tree-shake
// whatever it wants.
if (process.env.NODE_ENV === 'development') {
  const externalAPI = {
    ADD_DATA_SEGMENT_30S,
    ADD_DERIVED_SEGMENT_30S,
    ADD_COORDINATES_SEGMENT_30S,
    REMOVE_DATA_SEGMENT_30S,

    addDataSegment30s,
    addDerivedSegment30s,
    addCoordinatesSegment30s,
    removeDataSegment30s,
  };

  window &&
    window['This function does not exits'] &&
    window['This function does not exits'](externalAPI);
}
